import React, { Component } from "react";
class OrderBar extends Component {
  state = {
    order_price: 100,
  };
  render() {
    return (
      <div>
        <div className="order-bar displayInline">
          <div className="order-bar-text">Your Order</div>
          <div className="order-bar-price">
            <div className="order-bar-text">Order Price:</div>
            <div style={{ marginLeft: "10px" }}>
              Rs {this.state.order_price}/-
            </div>
          </div>
          <button className="order-bar-btn">View Your Order</button>
        </div>
      </div>
    );
  }
}

export default OrderBar;
