import React, { Component } from "react";
import Header from "./header";
import NavBar from "./navbar";
import BodyComponent from "./bodyComp";
import { Redirect } from "react-router-dom";
class LoginPage extends Component {
  constructor(props) {
    super(props);
    this.OnCLickLogin = this.OnCLickLogin.bind(this);
    this.handleChange = this.handleChange.bind(this);
  }
  state = {
    email: "",
    pass: "",
  };
  handleChange(event) {
    const target = event.target;
    this.setState({ [target.name]: target.value });
  }
  render() {
    return (
      <div className="LoginComp MakeBorder">
        <h4>UserName:</h4>
        <input
          type="email"
          name="email"
          className="form-control"
          placeholder="UserName"
          onChange={this.handleChange}
        />
        <h4 className="paddingTop">Password:</h4>
        <input
          type="password"
          name="pass"
          className="form-control"
          placeholder="Password"
          onChange={this.handleChange}
        />
        <button className="btn-default loginBtn" onClick={this.OnCLickLogin}>
          Login
        </button>
      </div>
    );
  }
  componentWillMount() {
    var logStatus = false;
    fetch("http://localhost:5000/isLoggedIn")
      .then((res) => res.text())
      .then((response) => (logStatus = response));
    if (logStatus == true) {
      //<Redirect to="/" />
    }
  }
  OnCLickLogin() {
    if (this.state.email == "" || this.state.pass == "") {
      this.setState({ error: "Please Enter All required Information" });
      console.log("please enter all info");
    } else {
      var data = {
        email: this.state.email,
        pass: this.state.pass,
      };
      const reqOptions = {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        credentials: "include",
        body: JSON.stringify(data),
      };
      console.log(data);
      fetch("http://localhost:5000/login", reqOptions)
        .then((res) => res.text())
        .then((response) => console.log(response));
    }
  }
}

export default LoginPage;
